<template>
  <div class="login">
    <van-cell-group class="loginform">
        <van-field
            v-model="userName"
            label="用户名"
            left-icon="contact"
        />
        <van-field
            v-model="passWord"
            label="密码"
            left-icon="eye-o"
        />
    </van-cell-group>
    <van-row type="flex" justify="center" class="loginbtn">
        <van-button type="primary" block @click="login">登陆</van-button>
    </van-row>
  </div>
</template>

<script>
import {
    post,
    LOGIN,
} from '../http';

export default {
    name: 'login',
    data() {
        return {
            userName: '',
            passWord: '',
        };
    },
    methods: {
        async login() {
            await post(LOGIN, {
                userName: this.userName,
                passWord: this.passWord,
            });
            this.$router.push('home');
            // console.log(data);
        },
    },
};
</script>

<style lang="scss">
.login{
    .loginform{
        width: 80%;
        margin: 0 auto;
        margin-top: 30%;
        border: 1px solid #07c160;
    }
    .loginbtn{
        width: 80%;
        margin: 0 auto;
        margin-top: 20px;
    }
}
</style>
