import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home';
import Login from '../views/Login';
import BeijingSaiChe from '../views/BeijingSaiChe';
import MnageHome from '../views/manage/MnageHome';
import userManage from '../views/manage/userManage';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'login',
        component: Login,
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
    },
    {
        path: '/home',
        name: 'home',
        component: Home,
    },
    {
        path: '/beijingsaiche',
        name: 'BeijingSaiChe',
        component: BeijingSaiChe,
    },
    {
        path: '/MnageHome',
        name: 'MnageHome',
        component: MnageHome,
    },
    {
        path: '/MnageHome/:id',
        name: 'MnageHome',
        component: MnageHome,
        children: [{
            path: '/MnageHome/userManage',
            name: 'userManage',
            component: userManage,
        }],
    },
    //   {
    //     path: '/about',
    //     name: 'about',
    //     // route level code-splitting
    //     // this generates a separate chunk (about.[hash].js) for this route
    //     // which is lazy-loaded when the route is visited.
    //     component: () => import(/* webpackChunkName: 'about' */ '../views/About.vue'),
    //   },
];

const router = new VueRouter({
    routes,
});

export default router;
